import React, { useState, useEffect } from 'react';
import { useStripe, useElements, CardElement } from '@stripe/react-stripe-js';
import { useBooking } from './BookingContext'; // Import the context

const Payment = () => {
  const stripe = useStripe();
  const elements = useElements();
  const { bookingData } = useBooking(); // Access the booking data from context

  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(false);
  const [serviceType, setServiceType] = useState('standard');
  const [price, setPrice] = useState(null);

  // Effect to trigger price calculation whenever bookingData changes
  useEffect(() => {
    if (bookingData && bookingData.distance) {
      console.log("Calculating price for distance:", bookingData.distance);
      calculatePrice();
    } else {
      console.log("Booking data or distance is missing!");
    }
  }, [bookingData]);

  const calculatePrice = async () => {
    try {
      const response = await fetch(
        'https://akreactrailsbackend-973ee20d6d7c.herokuapp.com/billings/calculate_price',
        {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            distance: bookingData.distance,
            
          }),
        }
      );
      const data = await response.json();
      console.log("Price calculated:", data);
      setPrice(data.price);
    } catch (error) {
      console.error('Error calculating price:', error);
    }
  };

  // Function to update the booking status to 'Paid'
  const updateBookingStatus = async () => {
    try {
      const response = await fetch(
        'https://akreactrailsbackend-973ee20d6d7c.herokuapp.com/ambulance_requests/update_status',
        {
          method: 'PATCH',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            ambulance_request_id: bookingData.ambulanceRequestId,
            status: 'Paid', // Mark the booking as paid
          }),
        }
      );
      const data = await response.json();
      if (response.ok) {
        console.log('Booking status updated successfully!');
      } else {
        console.error('Failed to update booking status', data.error);
      }
    } catch (error) {
      console.error('Error updating booking status:', error);
    }
  };

  const handlePayment = async (event) => {
    event.preventDefault();
    setLoading(true);
    setError(null);

    const cardElement = elements.getElement(CardElement);

    const { error, paymentMethod } = await stripe.createPaymentMethod({
      type: 'card',
      card: cardElement,
    });

    if (error) {
      setError(error.message);
      setLoading(false);
    } else {
      const response = await fetch(
        'https://akreactrailsbackend-973ee20d6d7c.herokuapp.com/billings',
        {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            ambulance_request_id: bookingData.ambulanceRequestId,
            amount: price || bookingData.amount, // Ensure price is set
            payment_method_id: paymentMethod.id,
          }),
        }
      );

      const data = await response.json();

      if (response.ok) {
        alert('Payment was successful!');
        // Update the booking status after payment success
        updateBookingStatus(); // Call to update the booking status
      } else {
        setError(data.error || 'Payment failed');
      }
      setLoading(false);
    }
  };

  return (
    <div style={styles.container}>
      <div style={styles.formContainer}>
        <h2 style={styles.heading}>Payment</h2>

        {/* Display Ambulance Request Details */}
        <div style={styles.infoContainer}>
          <p><strong>Ambulance Request ID:</strong> {bookingData.ambulanceRequestId}</p>
          <p><strong>Distance:</strong> {bookingData.distance} </p>
          <p><strong>Total Amount:</strong> K{price !== null ? price : bookingData.amount}</p>
        </div>

        <form onSubmit={handlePayment} style={styles.form}>
          <div style={styles.inputGroup}>
            <label style={styles.label}>Service Type:</label>
            <select
              value={serviceType}
              onChange={(e) => setServiceType(e.target.value)}
              style={styles.input}
            >
              <option value="standard">Standard</option>
              <option value="premium">Premium</option>
            </select>
          </div>

          <div style={styles.inputGroup}>
            <label style={styles.label}>Card Information:</label>
            <CardElement
              options={{
                style: {
                  base: {
                    fontSize: '16px',
                    color: '#424770',
                    '::placeholder': {
                      color: '#aab7c4',
                    },
                  },
                  invalid: {
                    color: '#9e2146',
                  },
                },
              }}
              style={styles.cardElement}
            />
          </div>

          {error && <p style={styles.error}>{error}</p>}

          <button type="submit" style={styles.button} disabled={!stripe || loading}>
            {loading ? 'Processing...' : 'Pay Now'}
          </button>
        </form>
      </div>
    </div>
  );
};

const styles = {
  container: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    height: '100vh',
    backgroundColor: '#f4f4f4',
    padding: '20px',
  },
  formContainer: {
    backgroundColor: 'white',
    borderRadius: '8px',
    padding: '30px',
    boxShadow: '0 4px 8px rgba(0, 0, 0, 0.1)',
    width: '100%',
    maxWidth: '500px',
  },
  heading: {
    textAlign: 'center',
    marginBottom: '20px',
    fontSize: '24px',
    color: '#333',
  },
  infoContainer: {
    marginBottom: '20px',
    backgroundColor: '#f9f9f9',
    padding: '10px',
    borderRadius: '5px',
    border: '1px solid #ddd',
  },
  form: {
    display: 'flex',
    flexDirection: 'column',
    gap: '15px',
  },
  inputGroup: {
    marginBottom: '15px',
  },
  label: {
    fontSize: '16px',
    fontWeight: '500',
    marginBottom: '5px',
    color: '#555',
  },
  input: {
    padding: '10px',
    fontSize: '16px',
    border: '1px solid #ccc',
    borderRadius: '4px',
    width: '100%',
  },
  cardElement: {
    border: '1px solid #ccc',
    borderRadius: '4px',
    padding: '10px',
    fontSize: '16px',
    marginBottom: '15px',
  },
  error: {
    color: 'red',
    fontSize: '14px',
    marginBottom: '15px',
  },
  button: {
    padding: '15px 0',
    fontSize: '18px',
    backgroundColor: '#4CAF50',
    color: 'white',
    border: 'none',
    borderRadius: '4px',
    cursor: 'pointer',
    transition: 'background-color 0.3s',
  },
};

export default Payment;
