import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import './HamburgerMenu.css'; // Keep the existing styles

const HamburgerMenu = () => {
  // State to manage menu toggle
  const [isOpen, setIsOpen] = useState(false);

  // Function to toggle the menu's open state
  const toggleMenu = () => {
    setIsOpen(!isOpen);
  };

  // Example default data for the payment page (replace with actual dynamic values if available)
  const paymentData = {
    distance: 10, // Replace with actual distance if dynamic
    serviceType: 'standard', // Replace with actual service type if dynamic
  };

  return (
    <div className="hamburger-menu">
      {/* Hamburger icon that toggles the menu */}
      <button 
        className={`hamburger-icon ${isOpen ? 'open' : ''}`} 
        onClick={toggleMenu}
      >
        <div className="line"></div>
        <div className="line"></div>
        <div className="line"></div>
      </button>

      {/* The menu itself, which opens and closes based on state */}
      <div className={`menu ${isOpen ? 'open' : ''}`}>
        <ul>
          <li><a href="/">Home</a></li>
          <li><a href="/about">About</a></li>
          <li><a href="/services">Services</a></li>
          <li><a href="/contact">Contact</a></li>
          <li><Link to="/login">Login</Link></li>

          {/* Payment link with data passed via state */}
          <li>
            <Link
              to={{
                pathname: '/payment',
                state: paymentData, // Attach payment data here
                
              }}
            >
              Payments
            </Link>
          </li>
        </ul>
      </div>
    </div>
  );
};

export default HamburgerMenu;
