import React, { useState, useEffect } from 'react';
import { GoogleMap, Autocomplete, Marker } from '@react-google-maps/api';
import { useNavigate } from 'react-router-dom';
import { useBooking } from './BookingContext'; 

const FrontPage = () => {
  const navigate = useNavigate();
  const { setBooking } = useBooking();
  const [apiKey, setApiKey] = useState('');
  const [name, setName] = useState('');
  const [phoneNumber, setPhoneNumber] = useState('');
  const [pickupLocation, setPickupLocation] = useState('');
  const [dropoffLocation, setDropoffLocation] = useState('');
  const [autocompletePickup, setAutocompletePickup] = useState(null);
  const [autocompleteDropoff, setAutocompleteDropoff] = useState(null);
  const [distance, setDistance] = useState('');
  const [error, setError] = useState('');

  useEffect(() => {
    // Fetch the API key from the Rails backend
    fetch('https://akreactrailsbackend-973ee20d6d7c.herokuapp.com/google_maps_key', {
      method: 'GET',
      headers: {
        'Authorization': 'Bearer eyJhbGciOiJIUzI1NiJ9.eyJ1c2VyX2lkIjoyfQ.43CEabzo0cLXC3PWwilhEQP23NjU5s-a54sv3UgG6nA' 
      }
    })
      .then(response => response.json())
      .then(data => {
        setApiKey(data.apiKey);
      })
      .catch(error => console.error('Error fetching API key:', error));
  }, []);

  const handlePlaceChanged = (type) => {
    if (type === 'pickup' && autocompletePickup) {
      const place = autocompletePickup.getPlace();
      if (place && place.formatted_address) {
        setPickupLocation(place.formatted_address);
      }
    } else if (type === 'dropoff' && autocompleteDropoff) {
      const place = autocompleteDropoff.getPlace();
      if (place && place.formatted_address) {
        setDropoffLocation(place.formatted_address);
      }
    }
  };

  const calculateDistance = () => {
    if (pickupLocation && dropoffLocation) {
      const service = new window.google.maps.DistanceMatrixService();
      service.getDistanceMatrix(
        {
          origins: [pickupLocation],
          destinations: [dropoffLocation],
          travelMode: window.google.maps.TravelMode.DRIVING,
        },
        (response, status) => {
          if (status === 'OK') {
            const element = response.rows[0].elements[0];

            if (element && element.status === 'OK') {
              const distanceText = element.distance.text;
              setDistance(distanceText);
              setBooking({
                distance: distanceText,
                pickupLocation,
                dropoffLocation,
              });
            } else {
              setError('Unable to calculate distance. Please check the locations.');
            }
          } else {
            setError('Error with Distance Matrix API request: ' + status);
          }
        }
      );
    }
  };

  useEffect(() => {
    if (pickupLocation && dropoffLocation) {
      calculateDistance();
    }
  }, [pickupLocation, dropoffLocation]);

  const handleBookAmbulance = () => {
    if (distance && pickupLocation && dropoffLocation) {
      setBooking({
        distance,
        pickupLocation,
        dropoffLocation,
      });
    } else {
      alert('Please ensure both pickup and dropoff locations are selected.');
    }
  };

  return (
    <div style={{ padding: '20px', maxWidth: '800px', margin: 'auto' }}>
      <h1>Welcome to MyAutoSOS</h1>
      <div style={{ marginBottom: '20px' }}>
        <label>
          <strong>Name:</strong>
          <input
            type="text"
            placeholder="Enter your name"
            value={name}
            onChange={(e) => setName(e.target.value)}
            style={{ display: 'block', width: '100%', margin: '10px 0', padding: '8px' }}
          />
        </label>
        <label>
          <strong>Phone Number:</strong>
          <input
            type="tel"
            placeholder="Enter your phone number"
            value={phoneNumber}
            onChange={(e) => setPhoneNumber(e.target.value)}
            style={{ display: 'block', width: '100%', margin: '10px 0', padding: '8px' }}
          />
        </label>
        <label>
          <strong>Pickup Location:</strong>
          <Autocomplete
            onLoad={(autocomplete) => setAutocompletePickup(autocomplete)}
            onPlaceChanged={() => handlePlaceChanged('pickup')}
          >
            <input
              type="text"
              placeholder="Enter pickup location"
              value={pickupLocation}
              onChange={(e) => setPickupLocation(e.target.value)}
              style={{ display: 'block', width: '100%', margin: '10px 0', padding: '8px' }}
            />
          </Autocomplete>
        </label>
        <label>
          <strong>Drop-off Location:</strong>
          <Autocomplete
            onLoad={(autocomplete) => setAutocompleteDropoff(autocomplete)}
            onPlaceChanged={() => handlePlaceChanged('dropoff')}
          >
            <input
              type="text"
              placeholder="Enter drop-off location"
              value={dropoffLocation}
              onChange={(e) => setDropoffLocation(e.target.value)}
              style={{ display: 'block', width: '100%', margin: '10px 0', padding: '8px' }}
            />
          </Autocomplete>
        </label>
      </div>

      <div style={{ marginTop: '10px', marginBottom: '20px' }}>
        <button
          onClick={handleBookAmbulance}
          style={{ padding: '10px 20px', fontSize: '16px' }}
        >
          Book Ambulance
        </button>
      </div>

      {/* Map Display */}
      {apiKey && (
        <div style={{ marginTop: '20px', height: '300px' }}>
          <GoogleMap
            id="google-map"
            mapContainerStyle={{ width: '100%', height: '100%' }}
            zoom={10}
            center={{
              lat: -15.4000 ,  // Lusaka latitude
              lng: 28.2871,   // Lusaka longitude
            }}
            
          >
            {pickupLocation && (
              <Marker
                position={{
                  lat: parseFloat(pickupLocation.split(",")[0]),
                  lng: parseFloat(pickupLocation.split(",")[1]),
                }}
              />
            )}
            {dropoffLocation && (
              <Marker
                position={{
                  lat: parseFloat(dropoffLocation.split(",")[0]),
                  lng: parseFloat(dropoffLocation.split(",")[1]),
                }}
              />
            )}
          </GoogleMap>
        </div>
      )}

      
    </div>
  );
};

export default FrontPage;
