import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import { Elements } from '@stripe/react-stripe-js';
import { loadStripe } from '@stripe/stripe-js';
import FrontPage from './services/FrontPage';
import Payment from './services/Payment';
import GoogleMapsLoader from './services/GoogleMapsLoader'; // Import your central loader
import HamburgerMenu from './services/HamburgerMenu'; // Import the HamburgerMenu component
import Login from './services/Login';

// Replace with your actual Stripe publishable key
const stripePromise = loadStripe('pk_test_51QBgyHFKf7TKPMnlc4JixHFfWXkyRSZU1Twp1xFu8PrKp8eMe1wXRD1LgqK3LWjK1njXY3W1oM7S3eJQjzYxOdil00p3RJiMQo');

function App() {
  const [apiKey, setApiKey] = useState('');
  const [distance, setDistance] = useState(''); // State for storing the distance

  useEffect(() => {
    // Fetch the API key from the backend
    fetch('https://akreactrailsbackend-973ee20d6d7c.herokuapp.com/google_maps_key', {
      method: 'GET',
      headers: {
        'Authorization': 'Bearer eyJhbGciOiJIUzI1NiJ9.eyJ1c2VyX2lkIjoyfQ.43CEabzo0cLXC3PWwilhEQP23NjU5s-a54sv3UgG6nA' // Replace with your actual token
      }
    })
      .then(response => response.json())
      .then(data => {
        setApiKey(data.apiKey); // Make sure this matches the key returned by your backend
      })
      .catch(error => console.error('Error fetching API key:', error));
  }, []);

  if (!apiKey) return <div>Loading API Key...</div>;

  return (
    <GoogleMapsLoader apiKey={apiKey}>
      <Router>
        <div className="App">
          {/* Add the HamburgerMenu component here */}
          <HamburgerMenu />
          
          <Routes>
            {/* Pass the setDistance function as a prop to FrontPage */}
            <Route path="/" element={<FrontPage setDistance={setDistance} />} />
            
            <Route path="/login" element={<Login />} />
            
            {/* Pass the distance as a prop to Payment */}
            <Route 
              path="/payment" 
              element={<Elements stripe={stripePromise}> 
                <Payment distance={distance} /> 
              </Elements>} 
            />
          </Routes>
        </div>
      </Router>
    </GoogleMapsLoader>
  );
}

export default App;
